/**
** 
**@constructor {Class} Note
** singleton  
**/
define('note',[],function(){
	function Note(){
		var CONST = arguments.callee;
		if(CONST._instance) return CONST._instance;	
		CONST.INIT = "init";
		CONST.STATIC_HTML    = "STATIC_HTML";//html 
		CONST.PLAIN_JSON     = "PLAIN_JSON";// json
		CONST.PLAIN_XML      = "PLAIN_XML";// xml 
		CONST.PLAIN_STRING      = "PLAIN_STRING";// xml 
		this.notification = {
			name: CONST.INIT,
			data:{}
		}
		
		CONST._instance = this;
	}
	return new Note;
});
