define('view/pieces/segs/elements/demo-confirm',[],function(){
	function Demo(){
				$("button.btn-primary").click(function(){
					$("#confirm-holder").confirm({
						content:""
					});
				});	
			
				$("button.btn-warning").click(function(){
					$("#confirm-holder").confirm({
						icon:"<i class='glyphicon glyphicon-globe'></i>",
						content:"?",
						btnOK:"",
						btnCANCEL:"CANCEL"
					});
				});			
	};
	
	return Demo;
});
