/***
**@Piece
**Piece   Segment  
**represent a piece of the Page
**head,footer,aside,article
**/
define('view/pieces/global-piece-header',['./segs/seg-head'],function(menu){
	extend(Header,Piece);
	function Header(){
		var _this = this;
		var _super = arguments.callee.parent;
		this.init = function(){
			this._DOM = $("div.nxdc-header[data-piece=header]");
			this.children = [menu];//seg  
			_super.init.call(_this);
		}
        
        this.update = function(msg){}
	};
	return new Header();
});
