define('view/pieces/segs/elements/demo-drop',[],function(){
	function Demo(){
            $("div.ndp-drop-wrapper[name=inline-drop]").drop({
                data:["","","","",""]
            });
            $("div.ndp-drop-wrapper[name=tree-drop]").drop({
                data:[{text:""},
					  {text:"",sub:[{text:""},
									  {text:""}]},
					  {text:"",sub:[{text:""},
									  {text:""}]},
					  {text:"",sub:[{name:"",
									   sub:[{name:""},
											{name:""}]}]},
					  {text:""},
					  {text:""}],
				caret:"glyphicon-menu-right"
            });  
            $("div.ndp-drop-wrapper[name=group-drop]").drop({
				type:3,
                data:[
					  {name:"",group:[{text:""},
									  {text:""}]},
					  {name:"",group:[{text:""},
									  {text:""}]},
					  {name:"",group:[{name:""},
									     {name:""},
									     {name:""}]}],
				caret:"glyphicon-menu-right"
            });
			
            $("div.ndp-drop-wrapper[name=disable-drop]").drop({
                data:[{name:"",disable:true},{name:""},"","",""]
            }); 
            $("div.ndp-drop-wrapper[name=line-drop]").drop({
                data:["",{name:""},{name:"",split:true},
					  "","","","",""]
            }); 
            $("div.ndp-drop-wrapper[name=checkbox-drop]").drop({
				type:4,
                data:["","","",""]
            }); 		
	}
	return Demo;
});
